@import 'colors';
@import 'fonts';

.display-flex {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.store-main-div {
  height: 200vh;
  width: 100%;
  background: $main-white;
}

.cart-stuff-div {
  height: 5.7vh;
  width: 75%;
  font-family: $rob;
  font-weight: 400;
  color: $shop-search;
  opacity: 0.8;
  font-size: 0.8vw;
  border-bottom: rgba(51, 51, 51, 0.1) 1px solid;
  margin-left: 12.5%;
}

.search {
  input {
    border: none;
    height: 4vh;
    width: 12vw;
    background: $main-white;
    font-size: 0.8vw;
  }
}

.login-signup {
  p {
    margin-right: 1.4vw;
  }
}

.buy {
  margin-left: 61%;
}

.cart {
  p {
    margin-right: 0.25vw;
  }
}

.checkout {
  margin-left: 1.15vw;
}

.products-div {
  width: 75%;
  margin-left: 12.5%;
}

.shop-nav {
  width: 20%;
  font-family: $oswald;
  color: $featured-grey;
  font-weight: bold;
  font-size: 2.2vw;
}

.shop-nav {
  ul {
    margin-top: 11%;
  }
}

.shop-nav {
  li {
    margin-bottom: .6vh;
  }
}
